/* harmony export (binding) */ __webpack_require__.d(__webpack_exports__, "a", function() { return schedule; });
var schedule=[{time:'8:00 - 9:00',day:1,title:'Registration and Breakfast',isSavable:false},{time:'9:00 - 9:15',day:1,title:'Introduction',isSavable:false},{time:'9:15 - 10:00',day:1,title:'Ken Wheeler',speaker:'',isSavable:false},{time:'10:00 - 10:30',day:1,title:'React & GraphQL - Bringing Simplicity and Correctness to Client Side Development',speaker:'Chris Toomey',description:'One of React\u2019s stronger attributes is the component model which allows us to combine behavior and presentation of functional units as opposed to along the arbitrary technology focused separation previously used, but unfortunately had less to say about data needs. With GraphQL we can have an ideal final slide to allow our components to fully encapsulate all of their requirements while keeping things modular and reusable. In this talk we\u2019ll cover both the theory and practice of client development with React and GraphQL, along the way highlighting the areas where we can even more strongly leverage its power and flexibility.'},{time:'10:30 - 11:00',day:1,title:'Break ☕',isSavable:false},{time:'11:00 - 11:45',day:1,title:'Horror Stories From Hacking the DOM Into a Native Platform',speaker:'Vincent Riemer',description:'You don\u2019t go building something like React Native DOM without anticipating a few bruises along the way. Most of its development was a loop of hitting roadblocks and finding \u201Ccreative\u201D solutions around them. In this talk I\u2019ll highlight some of of the largest roadblocks faced in React Native DOM\u2019s development and the (occasionally absurd) solutions it took to overcome them such as Image\u2019s tintColor prop, layout animations, the back handler, and more.'},{time:'11:45 - 12:15',day:1,title:'From Sketch to Code: Designing a Component Kit',speaker:'Samantha Bretous',description:'Working on multiple enterprise projects that use the same components can get a little overwhelming. I will talk through how to create an effective workflow by showing examples of what worked and did not work for our team, from design QA to constant props. As well as how we tackled component documentation that stays up to date with API changes. Lastly, we will explore new ideas that we will want to explore like working with AirBnb\'s react-sketchapp.'},{time:'12:15 - 1:30',day:1,title:'Lunch',isSavable:false},{time:'1:30 - 2:00',day:1,title:'State of Redux',speaker:'Mark Erikson',description:''},{time:'2:00 - 3:00',day:1,title:'Lightning Talks ⚡',lightningTalks:['Autumn Crossan','Erin Fox','Shawn Swyx Wang','Cole Turner'],isSavable:false},{time:'3:00 - 3:30',day:1,title:'The Art of Humanizing Pull Requests',speaker:'Ankita Kulkarni',description:'Code reviews are an important part of a developer\u2019s life. It\u2019s very easy to perform code reviews, but hard to do it with empathy. In today\u2019s online world, online criticism can be misinterpreted easily. Hence in this talk, we will go through what is a pull request, how to effectively create one, how to give constructive feedback and also how to respond to one using empathy. '},{time:'3:30 - 4:00',day:1,title:'Break ☕',isSavable:false},{time:'4:00 - 4:30',day:1,title:'Custom CSS is the Path to Inconsistent UI',speaker:'Artem Sapegin',description:'Custom CSS is the simplest way to make the UI inconsistent, especially if we\u2019re talking about spacing between UI elements, font sizes or colors. When developers have too much freedom in defining the UI, they will do things differently. The issue could be solved by avoiding any custom CSS on the app level and having a set of primitive components that allow developers to glue UI together in a consistent way.'},{time:'4:30 - 5:00',day:1,title:'Streaming React for Faster UIs',speaker:'Tejas Kumar',description:'The talk aims to shed some light on two things: a) React DOM\'s stream renderer that allows React components to be rendered to streams on the server and piped to a browser\'s response, and b) Browsers\' added support for streams that harness their streaming HTML parsers.  The way these technologies combine allow the creation of performant, powerful web applications with impressive time-to-interactive metrics.'},{time:'5:00 - 5:30',day:1,title:'Building AR Apps with React Native',speaker:'Vladimir Novick',description:'With the release of ARKit and ARCore by Apple and Google we see various Augmented reality apps created for iOS and Android. In this talk we will see how we can build AR apps with React Native, ARKit and ARCore. '},{time:'6:00 - 9:00',day:1,title:'Party! 🎉',isSavable:false},{time:'8:30 - 9:30',day:2,title:'Breakfast',isSavable:false},{time:'9:30 - 10:15',day:2,title:'The Case for Whimsy',speaker:'Joshua Comeau',description:'I wish the internet was more fun. The web is an incredible platform, and can do incredible things. This talk looks at novel, quirky, whimsical animations and interactions that make our products better and more enjoyable to use. For example, at Khan Academy, we fill the user\u2019s screen with confetti rain when they get 100% correct on an exercise. I\u2019ll talk about how we drew on 2D physics to make the confetti feel real. Make no mistake; while this talk straddles the line between engineering and UX design, this is very much a React talk. We\'ll look at how to abstract motion and whimsical behavior into reusable components, how to fold up the DOM like origami, and how to do SVG/canvas animation in a React-friendly way.'},{time:'10:15 - 10:45',day:2,title:'Beyond Web-Apps: React, Javascript and WebAssembly to Port Legacy Native Apps',speaker:'Florian Rival',description:'Can we create or port an existing native desktop application to a complete "React powered" web-app matching (or even exceeding) the quality of the legacy desktop app? Sounds like a good challenge! \uD83E\uDD14And this is what I decided to do with my game making software. \nThis talk will explain all the problems I had and solutions that I used to port my native app written in C++, to a full React + WebAssembly app - while still keeping all existing features! Spoiler alert: At the end, it works.'},{time:'10:45 - 11:15',day:2,title:'Break ☕',isSavable:false},{time:'11:15 - 11:45',day:2,title:'"Forbidden Planet" Cured My Imposter Syndrome',speaker:'Jen Luker',description:'Come with me on an adventure with a heroic robot, a mad scientist, a feminist, a pilot, and our monster, imposter syndrome. Let me share how a sci-fi movie from 1956, starring Walter Pidgeon, Anne Francis, and Leslie Nielsen, taught me how to recognize imposter syndrome not as the monster hellbent on ruining my romance with an ever-evolving development ecosystem, but as a tool to prevent burnout.'},{time:'11:45 - 12:15',day:2,title:'Progressive React',speaker:'Houssein Djirdeh',isSavable:false},{time:'12:15 - 1:30',day:2,title:'Lunch',isSavable:false},{time:'1:30 - 2:00',day:2,title:'Introducing Undux: Dead Simple, Typesafe State Management for React',speaker:'Boris Cherny'},{time:'2:00 - 3:00',day:2,title:'Lightning Talks ⚡',lightningTalks:['Christina Keelan Cottrell','Josh Pollock','Jason Clark','Andrew Kaiser'],isSavable:false},{time:'3:00 - 3:30',day:2,title:'Deterministic Mocking with GraphQL and Apollo: Lunar Launch',speaker:'Hillary Bauer and Mark Faga',isSavable:false,description:'Tests are an integral part of the development life cycle, but it isn\u2019t always clear what the most efficient way to write them is. Learn some of the Dos & Don\'ts when it comes to testing your React app with Apollo components. We\u2019ll explore: When is a unit test appropriate? When should you lean on integration tests? And how the heck do you dynamically mock GraphQL responses with data specific to your tests? ezCater has solved these problems and is happy to announce its newest edition to the open source community: Lunar, a package to help you mock GraphQL data for your integration tests!'},{time:'3:30 - 4:00',day:2,title:'Break ☕',isSavable:false},{time:'4:00 - 4:30',day:2,title:'Large Scale Migrations: Using Recast and jscodeshift to Scale',speaker:'Francois Ward',description:'Keeping a tech stack up to date is hard. Keeping it up to date when it has grown to millions of lines of code or thousands of packages and repositories  is harder. Leaning on automation helps, but how do you automate code changes?  In this talk, Francois goes over the various parsers and code transformation tools such as Babel, TypeScript, Recast and jscodeshift, where they fit in the ecosystem, and how to use them to automate code changes across hundreds of projects to keep your React (or any other) apps in sync with the rest of the world. Attendees will learn about the various ways to work with JavaScript ASTs (there\'s a lot!) as well as how to orchestrate migrations and pull requests when manual no longer works.'},{time:'4:30 - 5:00',day:2,title:'React: A Romance of Many Dimensions',speaker:'Matt Hamil',description:'Is VR an approachable medium for JavaScript developers? Can we, as front-end developers, use what we already know to build a 3D world? Can we use React to reach the 4th dimension? Yes! React360 is here to let us create delightful VR experiences in the browser.'}];

